        background: none !important;
    }
    select#ddlwarningMessageTypeId {
        width: 100% !important;
        padding: 8px 15px 5px 11px !important;
        margin-left: 0;
        background-color: #f9d99b;
        border: 1px solid #c89025;
        color: #604106;
        font-size: 12px;
        margin-top: 0px;
    }

    div#divruleflowdatafsmshow {
        min-width: 100%;
