    #divRuleTargetResultAddDialog .fc_parentchild .arrowdown {
        background-image: url(/Content/OrganizationChart/FlowChart_DownArrow_90.png);
        background-repeat: no-repeat;
        padding-bottom: 40px;
    }
    .formsfieldmainunique_condition {
         margin-top: 0px !important; 
    }

    .fc_parentchild .arrowdown {
        width: auto;
        height: auto;
        display: block;
        clear: both;
        margin-bottom: 60px;
        position: relative;
    }
    div#li_0 .innercontbase {
        width: 100%;
    }
    div#li_0 .innercont {
        min-width: 100%;
    }

    div#divruleflowdatafsmshow .arrowdown {
        margin-bottom: 20px;
    }
    .f30{font-size:30px;}
    .fc_parentchild li {margin-bottom: 0px !important;}
    .flowfsm .fc_parentchild .div-li.arrowdown:last-child::after { display:none;}
</style>